.Home {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--background-primary);
  color: var(--font-primary);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Home-wrapper {
  max-width: 25rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Home-image {
  margin-bottom: -210px;
  height: 420px;
  width: auto;
  cursor: pointer;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}

.Home-image:hover {
  -webkit-transform: rotateY(15deg);
          transform: rotateY(15deg);
}

.Home-title {
  margin-bottom: 2rem;
  text-align: center;
  color: var(--highlight);
}

.Home-social {
  margin: 1rem 0;
}

.Home-social a {
  color: inherit;
}

.Home-social a:first-child {
  margin-right: 1rem;
}

.Home-icon {
  cursor: pointer;
  transition: color 0.4s ease;  
}

.Home-icon:hover {
  color: var(--highlight);
}

@media screen and (max-width: 799px) {
  .Home-wrapper {
    max-width: 20rem;
    margin-top: 6rem;
    margin-bottom: 6rem;
  }
  
  .Home-image {
    margin-bottom: -120px;
    height: 240px;
    width: auto;
  }
}

.Callout {
    width: 100%;
}

.Callout-wrapper {
    max-height: 8rem;
    padding: 0 1.5rem;
    background-color: var(--background-secondary);
    color: var(--font-secondary);

    display: flex;
    align-items: center;
}

.Callout-spacer {
    width: 2rem;
}

.Callout-image {
    width: 10rem;
    height: 10rem;
    border: 6px solid var(--font-primary);
    border-radius: 50%;
    flex-shrink: 0;
    flex-grow: 0;
}

.Callout-title {
    color: inherit;
    margin-bottom: 0.5rem;
}

.Callout-description {
    color: inherit;
    font-size: 16px;
    line-height: 20px;
}

@media screen and (max-width: 799px) {   
    .Callout-wrapper {
      max-height: 6rem;
      padding: 0 1rem;
    }
  
    .Callout-image {
      width: 7rem;
      height: 7rem;
      border: 4px solid var(--font-primary);
    }
  
    .Callout-title {
      margin-bottom: 0.2rem;
      font-size: 14px;
    }
    
    .Callout-description {
      font-size: 12px;
      line-height: 14px;
    }
  }

.About {
  width: 100%;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.About-wrapper {
  max-width: 36rem;
  margin-top: 18.5rem;
  margin-bottom: 7rem;
}

.About-tag {
  margin-left: -8rem;
  margin-bottom: 2rem;
  color: var(--highlight);
}

.About-text {
  color: var(--font-secondary);
}

.About-callout {
  margin-top: 7rem;
}

.About-callout-spacer {
  height: 3rem;
}

@media screen and (max-width: 1000px) {
  /* .About-wrapper {
    max-width: 28rem;
  } */

  .About-tag {
    margin-left: 0;
  }
  
}

@media screen and (max-width: 799px) {
  .About-wrapper {
    max-width: 20rem;
    margin-top: 13.5rem;
    margin-bottom: 5rem;
  }

  .About-tag {
    margin-bottom: 1.5rem;
  }

  .About-callout {
    margin-top: 5rem;
  }

  .About-callout-spacer {
    height: 2rem;
  }
}

.Resume {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--background-secondary);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Resume-wrapper {
  max-width: 36rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Resume-tag {
  margin-left: -10rem;
  margin-bottom: 2rem;
  color: var(--highlight);
}

.Resume-hr {
  height: 2px;
  margin-bottom: 1rem;
  background-color: var(--highlight);
}

.Resume-spacer-vertical {
  height: 3rem;
}
  
@media screen and (max-width: 1000px) {
  /* .Resume-wrapper {
    max-width: 28rem;
  } */

  .Resume-tag {
    margin-left: 0;
  }
}

@media screen and (max-width: 799px) {
  .Resume-wrapper {
    max-width: 20rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .Resume-tag {
    margin-bottom: 1.5rem;
  }
}

.Card {
    margin: 0 1rem;
    padding: 0;
    width: calc(100% - 2rem);
    height: auto;
    background-color: var(--background-secondary);
    color: var(--font-secondary);

    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Card-image {
    width: 10rem;
    height: 10rem;
    margin-top: -4rem;
    border: 6px solid var(--font-primary);
    border-radius: 50%;
}

.Card-title {
    margin-top: 2rem;
    padding: 0 1rem;
}

.Card-description {
    margin-top: 0.5rem;
    padding: 0 1rem 1rem 1rem;
    text-align: left;
}

.Slider {
    width: 100%;
    overflow-x: hidden;
}

.Slider-controls {
    margin-top: 2rem;
    display: flex;
    align-items: center;
}

.Slider-control {
    padding: 0 1rem;
    font-size: 2rem;
    color: var(--font-secondary);
    cursor: pointer;
    transition: color 0.4s ease;
}

.Slider-control:hover {
    color: var(--highlight);
}

.Slider-progress {
    width: 100%;
    height: 4px;
    background-color: var(--font-secondary);
}

.Slider-progress-fill {
    height: inherit;
    background-color: var(--highlight);
    transition: width 0.4s ease-in-out;
}

.Slider-slides {
    width: 100%;
    height: auto;
    margin-top: 4rem;
    display: flex;
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}

.Projects {
  width: 100%;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Projects-wrapper {
  width: 36rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.Projects-tag {
  margin-left: -10rem;
  margin-bottom: 2rem;
  color: var(--highlight);
}

@media screen and (max-width: 1000px) {
  /* .Projects-wrapper {
    width: 28rem;
  } */

  .Projects-tag {
    margin-left: 0;
  }
}

@media screen and (max-width: 799px) {
  .Projects-wrapper {
    width: 20rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .Projects-tag {
    margin-bottom: 1.5rem;
  }
}

.Contact {
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: var(--background-primary);
    color: var(--font-primary);

    display: flex;
    flex-direction: column;
    align-items: center;
  }

.Contact-wrapper {
    width: 36rem;
    margin-top: 6rem;
    margin-bottom: 6rem;
}

.Contact-tag {
    margin-left: -10rem;
    margin-bottom: 2rem;
    color: var(--highlight);
}

.Contact-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    justify-items: center;
    align-items: center;
    grid-row-gap: 4rem;
    row-gap: 4rem;
}

.Contact-avatar {
    width: 12rem;
    height: 12rem;
    border: 6px solid var(--font-primary);
}

.Contact-details {
    word-break: break-all;
}

.Contact-link {
    margin-top: 0.5rem;
    color: var(--highlight);
}

.Contact-link:hover {
    box-shadow: 0 2px 0 0 var(--highlight);
    transition: box-shadow 0.4s;
}

.Contact-spacer-vertical {
    height: 1rem;
}

@media screen and (max-width: 1000px) {
    /* .Contact-wrapper {
        width: 28rem;
    } */

    .Contact-tag {
        margin-left: 0;
    }
}

@media screen and (max-width: 799px) {
    .Contact-wrapper {
        width: 20rem;
        margin-top: 4rem;
        margin-bottom: 4rem;
    }

    .Contact-tag {
        margin-bottom: 1.5rem;
    }

    .Contact-content {
        grid-row-gap: 2rem;
        row-gap: 2rem;
    }

    .Contact-avatar {
        width: 10rem;
        height: 10rem;
    }
}

.Reset {
    z-index: 200;
    position: fixed;
    top: 2rem;
    right: 2rem;
    cursor: pointer;

    -webkit-transform: translateX(8rem);

            transform: translateX(8rem);
    transition: -webkit-transform 0.4s ease;
    transition: transform 0.4s ease;
    transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.show-reset {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.Reset-icon {
    padding: 2px;
    color: var(--font-secondary);
    font-size: 3rem;
    transition: color 0.4s ease;
}

.Reset:hover svg {
    color: var(--highlight);
}

@media screen and (max-width: 799px) {
    .Reset {
        top: 1rem;
        right: 1rem;
    }

    .Reset-icon {
        font-size: 2.5rem;
    }
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
  /* color: var(--main-font-color); */
  /* background-color: var(--main-bg-color); */
}

/* CSS Vars */
:root {
  --font-primary: #ffffff;
  --font-secondary: #272f30;
  --background-primary: #5F6667;
  --background-secondary: #ededed;
  --background-dark: #383F40;
  --highlight: #DCBA6C;
  --shadow: rgb(50, 50, 50, 0.5);
}
/* END CSS Vars */

html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 0;
  padding: 0;
  font-family: "Merriweather";
  font-size: 50px;
  font-weight: 700;
  letter-spacing: 2px;
}

h2 {
  margin: 0;
  padding: 0;
  font-family: "Merriweather";
  font-size: 36px;
  font-weight: 700;
}

h3 {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
}

h4 {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

p {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

a {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
}

@media screen and (max-width: 799px) {
  h1 {
    font-size: 40px;
  }
  
  h2 {
    font-size: 22px;
  }
  
  h3 {
    font-size: 18px;
    line-height: 24px;
  }
  
  h4 {
    font-size: 16px;
    line-height: 20px;
  }
  
  p {
    font-size: 16px;
    line-height: 20px;
  }
  
  a {
    font-size: 16px;
    line-height: 20px;
  }
}

